import React from 'react';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActionArea from '@material-ui/core/CardActionArea';
import TextField from '@material-ui/core/TextField';
import ArrowForwardIcon from '@material-ui/icons/ArrowForwardSharp';
import UnfoldMoreIcon from '@material-ui/icons/UnfoldMoreSharp';
import CardMedia from '@material-ui/core/CardMedia';
import Grid from '@material-ui/core/Grid';
import { Link } from '@reach/router';

import Drama from '../models/Drama';
import { Layout, Wrapper, Arrow } from '../components';
import dramaDefault from '../utils/drama';
import '../components/styles.scss';

interface IndexPageState {
  drama: Drama;
}

export default class IndexPage extends React.Component<{}, IndexPageState> {
  constructor(props: any) {
    super(props);
    let drama: Drama;
    if (typeof window !== `undefined`) {
      const dramaJson: any = window.localStorage.getItem('drama');
      drama = JSON.parse(dramaJson) || dramaDefault;
    } else {
      drama = dramaDefault;
    }

    this.state = {
      drama,
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleOnSave = this.handleOnSave.bind(this);
  }

  handleInputChange(event: any) {
    const target = event.target;
    const name: string = target.name;
    const value: any = target.value;
    const drama: Drama = Object.assign({}, this.state.drama, { [name]: value });

    this.setState({ drama });
  }

  handleOnSave() {
    if (typeof window !== `undefined`) {
      window.localStorage.setItem('drama', JSON.stringify(this.state.drama));
    } else {
      alert('保存失败');
    }
  }

  public render() {
    const drama: Drama = this.state.drama;

    return (
      <Layout>
        <Wrapper>
          <Card>
            <CardContent className={`card-title`}>生成广播剧帖</CardContent>
            <CardActionArea>
              <CardContent>
                <Grid container>
                  <Grid item xs={2} className={`theme-label`}>
                    主题
                  </Grid>
                  <Grid item xs={3}>
                    <CardMedia className={`theme-cover`} image="https://ws3.sinaimg.cn/large/006tNbRwgy1fy8xkbdep1j31540u0myo.jpg" />
                  </Grid>
                  <Grid item xs={5}>
                    <h4>现代简单黑白灰主题</h4>
                    <p>暂无其他主题</p>
                  </Grid>
                  <Grid item xs={2}>
                    <UnfoldMoreIcon className={`unflod-theme`} />
                  </Grid>
                </Grid>
              </CardContent>
            </CardActionArea>
            <CardContent>
              <TextField
                variant="filled"
                value={drama.title}
                onChange={this.handleInputChange}
                fullWidth={true}
                label="标题"
                name="title"
                required={true}
              />
              <TextField
                variant="filled"
                value={drama.subtitle}
                onChange={this.handleInputChange}
                fullWidth={true}
                label="副标题"
                name="subtitle"
                required={true}
              />
              <TextField
                variant="filled"
                value={drama.episode}
                onChange={this.handleInputChange}
                fullWidth={true}
                label="期数"
                name="episode"
                required={true}
              />
            </CardContent>
            <Arrow>
              <Link to="/generate" onClick={this.handleOnSave}>
                <Button size="large" className={`button`}>
                  下一步
                  <ArrowForwardIcon />
                </Button>
              </Link>
            </Arrow>
          </Card>
        </Wrapper>
      </Layout>
    );
  }
}
